<template>
     <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
            <v-card class="elevation-12">
              <v-toolbar
                color="primary"
                dark
                flat
              >
                <v-toolbar-title>NDT-LAN</v-toolbar-title>
                <v-spacer />
              </v-toolbar>
              <v-card-text>
                <v-form @submit.prevent="login">
                  <v-text-field
                    label="E-post"
                    name="login"
                    type="text"
                    v-model="username"
                  />

                  <v-text-field
                    id="password"
                    label="Passord"
                    name="password"
                    type="password"
                    v-model="password"
                  />
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-btn color="primary" @click.prevent="login">Logg inn</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
</template>

<script>
  import { mapActions } from 'vuex'

  export default {
    name: 'Login',

    data: () => ({
      username: '',
      password: ''
    }),

    methods: {
      ...mapActions(['authenticate']),
      async login () {
        try {
          const { username, password } = this
          await this.authenticate({ username, password })
          this.$router.push({ name: 'Home' })
        } catch (e) {
          alert('Feil brukernavn eller passord')
        }
      }
    }
  }
</script>
