import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

const client = Axios.create({
  baseURL: 'https://api.netflexapp.com/v1'
})

export default new Vuex.Store({
  state: {
    auth: null
  },

  getters: {
    auth: state => ({ username: state.auth.publicKey, password: state.auth.privateKey })
  },

  mutations: {
    SET_AUTH(state, { publicKey, privateKey }) {
      state.auth = {
        publicKey,
        privateKey
      }
    }
  },

  actions: {
    async authenticate ({ commit }, { username, password }) {
      const response = await client.get('auth/ndtlan', {
        auth: { username, password }
      })

      const publicKey = response.data.public_key
      const privateKey = response.data.private_key

      commit('SET_AUTH', { publicKey, privateKey })
    }
  },

  plugins: [createPersistedState()]
})
